<template>
	<div id="mall-goods-seach">
		<public-header title="商品搜索"></public-header>
		<div class="content">
			<form action="">
				<div class='search-header van-hairline--bottom'>
					<div class="search-content">
						<van-icon name="search"/>
						<input v-model="keyword" @keyup.13="onTapToSearch" ref="searchInput" type="text" class="search-input" placeholder="请输入商品名称">
					</div>
					<div class="search-btn" @click="onSearch(true)">搜索</div>
				</div>
			</form>

			<template v-if="records.page == 1">
				<div class='rmd-history' v-if="hotGoods && hotGoods.length > 0">
					<div class='search-rmd-wrap'>
						<div class='search-rmd-title '>
							<div class="van-hairline--bottom">热门搜索</div>
						</div>
						<div class='search-rmd'>
							<div v-for="(item, index) in hotGoods" @click="onSearch(true, item.title)" :key="'hot' + index">{{item.title}}</div>
						</div>
					</div>
				</div>
				<div class='rmd-history' v-if="searchHistory && searchHistory.length > 0">
					<div class='search-rmd-wrap'>
						<div class='search-rmd-title'>
							<div class="van-hairline--bottom">搜索历史</div>
							<div class='icon icon-delete' @click="onDelHistory"></div>
						</div>
						<div class='search-rmd'>
							<div v-for="(item, index) in searchHistory" @click="onSearch(true, item)" :key="'h' + index">{{item}}</div>
						</div>
					</div>
				</div>
			</template>
			<div class="mall-goods-seach-warp">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onSearch(false)"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
				>
					<div class="goods-list">
						<div class="goods-item van-hairline--bottom" v-for="goodsItem in records.data" :key="goodsItem.id">
							<div class="goods-thumb" @click="onDetail(goodsItem.id, goodsItem.sid)">
								<img class="img-100" :src="goodsItem.thumb" alt="">
							</div>
							<div class="goods-info">
								<div class="w-100 ellipsis font-14 font-500" @click="onDetail(goodsItem.id, goodsItem.sid)">{{goodsItem.title}}</div>
								<div class="c-gray flex font-12 margin-5-t" @click="onDetail(goodsItem.id, goodsItem.sid)">
									<span>已售{{goodsItem.sailed}}</span>
									<span class="margin-15-l">赞{{goodsItem.comment_good}}</span>
								</div>
								<div class="svip-price-tips margin-5-t" v-if="goodsItem.svip_status == 1 && !goodsItem.discount_price">
									<div class="svip-price">
										<i class="icon icon-crownfill"></i>
										<span>{{goodsItem.discount}}折</span>
									</div>
									<div class="svip-activity">超级会员专享</div>
								</div>
								<div class="font-500 c-danger font-14 flex margin-5-t" @click="onDetail(goodsItem.id, goodsItem.sid)">
									<span>{{Lang.dollarSign}}{{goodsItem.price}}{{goodsItem.unitname_cn}}</span>
									<div class="flex margin-10-l" v-if="goodsItem.svip_status != 1 || goodsItem.discount_price > 0">
										<template v-if="goodsItem.discount">
											<van-icon name='tag'></van-icon>
											<span class="font-12">{{goodsItem.discount}}折 限{{goodsItem.max_buy_limit}}份 {{goodsItem.unitnum > 1 ? goodsItem.unitnum_multi_cn + '购' : ''}}</span>
										</template>
										<template v-else-if="goodsItem.unitnum > 1">
											<van-icon name='tag'></van-icon>
											<span class="font-12">{{goodsItem.unitnum_multi_cn}}购</span>
										</template>
									</div>
									<div class="flex margin-10-l" v-else-if="goodsItem.unitnum > 1">
										<van-icon name='tag'></van-icon>
										<span class="font-12">{{goodsItem.unitnum_multi_cn}}购</span>
									</div>
									<div class="kabao-price-wrap" v-if="goodsItem.kabao_status == 1 && !goodsItem.discount_price">
										<span class="kabao-price">
											￥{{goodsItem.kabao_price}}
											<span class="font-14">{{goodsItem.unitname_cn}}</span>
										</span>
										<span class="kabao-label">
											<i class="icon icon-vip"></i>
											<span>{{goodsItem.kabao_group_title}}</span>
										</span>
									</div>
								</div>
								<div class="goods-operation">
									<simple-goods-handle
										:goods="goodsItem"
										:cartIndex="goodsItem.cartIndex"
										mode="all"
									>
									</simple-goods-handle>
								</div>
							</div>
						</div>
					</div>
				</van-list>
			</div>
			<div class="no-data" v-if="records.empty">
				<img src="static/img/order_no_con.png" alt="">
				<p>暂无相关商品，敬请期待</p>
			</div>
		</div>
		<div class="cartop" v-if="cartTotalNum > 0">
			<router-link :to="util.getUrl({path: '/mall/pages/cart/index', query: {}})">
				<div class="num">{{cartTotalNum}}</div>
				<div class='icon icon-cartfill'></div>
			</router-link>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import GoodsHandle from '@/components/goodsHandle'
	import SimpleGoodsHandle from '@/components/simpleGoodsHandle'
	import {getData, getRecords} from "@/controller/funcCommon"
	import {mapState, mapMutations} from 'vuex'
	export default {
		data() {
			return {
				showPreLoading: true,
				records: {
					page: 1,
					psize: 10,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				cart: {
					num: 0,
				},
				from: '',
				type: 'takeout',
				table_id: 0,
				table: {},
				store: {},
				hotGoods: [],
				keyword: '',
				searchHistory: [],
				activetabs: 0,
				showMoveDot: [],
			}
		},
		components: {
			PublicHeader,
			GoodsHandle,
			SimpleGoodsHandle
		},
		methods: {
			...mapMutations([
				'replaceStore', 'replaceCart', 'replaceAllcart'
			]),

			onTapToSearch(e) {
				var that = this;
				var inputDom = that.$refs['searchInput'];
				inputDom.blur();
				this.onSearch(true);
			},

			onSearch(force, keyword) {
				var that = this;
				if(!keyword) {
					if(that.keyword) {
						var is_exist = that.searchHistory.indexOf(that.keyword);
						if(is_exist == -1) {
							that.searchHistory.push(that.keyword);
							that.util.setStorage('mallSearchHistory.goods', that.searchHistory);
						}
					}
				} else {
					that.keyword = keyword;
				}
				if(!that.keyword) {
					that.util.$toast('请输入商品名称', '', 1000);
					return;
				}
				that.records.loading = true;
				getRecords({
					vue: that,
					force: force,
					recordsName: 'goods',
					url: 'wmall/mall/goods/search',
					data: {
						is_search: 1,
						keyword: that.keyword
					},
					success: function(res) {
						if(!that.records.empty) {
							that.replaceAllcart(res.carts);
						}
					}
				})
			},

			onLoad() {
				var that = this;
				that.searchHistory = that.util.getStorage('mallSearchHistory.goods');
				if(!that.searchHistory) {
					that.searchHistory = [];
				}
				getData({
					vue: that,
					url: 'wmall/mall/goods/search',
					data: {
						is_search: 0
					},
					success: function(res) {
						that.hotGoods = res.hotGoods;
					}
				})
			},

			onDetail(goodsid, sid) {
				if(this.table_id > 0) {
					this.$router.push(this.util.getUrl({path: 'tangshi/pages/table/goodsDetail', query: {id: goodsid, sid: sid, table_id: this.table_id}}));
				} else {
					this.$router.push(this.util.getUrl({path: 'pages/store/goodsDetail', query: {id: goodsid, sid: sid, pindan_id: this.pindan_id}}));
				}
			},

			onDelHistory() {
				this.$dialog.confirm({
					message: '确定清除历史记录吗'
				}).then((res) => {
					if (res == 'confirm') {
						this.util.removeStorage('mallSearchHistory.goods');
						this.searchHistory = [];
					}
				})
			}
		},
		computed: {
			...mapState([
				'icart', 'allcart'
			]),
			cartTotalNum: function() {
				var num = 0;
				var allcart = this.allcart;
				if(allcart) {
					for(var sid in allcart) {
						for(var i in allcart[sid]['cart']['data']) {
							for(var j in allcart[sid]['cart']['data'][i]) {
								num += allcart[sid]['cart']['data'][i][j]['num'];
							}
						}
					}
				}
				return num;
			}
		},
		created() {

		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#mall-goods-seach .search-header{
		background-color: #fff;
		width: 100%;
		height: 50px;
		box-sizing: border-box;
		padding:10px;
		display:flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
	}
	#mall-goods-seach .search-header .search-content{
		width: 90%;
		height: 100%;
		border-radius: 40px;
		position: relative;
	}
	#mall-goods-seach .search-header .search-content .van-icon{
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 30px;
		line-height: 28px;
		text-align: center;
		font-size: 14px;
	}
	#mall-goods-seach .search-header .search-content .search-input{
		width: 100%;
		height: 100%;
		line-height: 28px;
		border-radius: 40px;
		padding-left: 25px;
		box-sizing: border-box;
		background: #f2f2f2;
		font-size: 14px;
	}
	#mall-goods-seach .search-header .search-btn{
		font-size: 14px;
		color: #FF2D4B;
	}
	#mall-goods-seach .rmd-history{
		margin-bottom: 10px;
		background-color: #f5f5f5;
	}
	#mall-goods-seach .rmd-history .search-rmd-title{
		position: relative;
		padding-left: 15px;
		font-size: 15px;
		line-height: 38px;
		color: #656565;
		background: #fff;
	}
	#mall-goods-seach .rmd-history .search-rmd-title .icon{
		position: absolute;
		top: 0;
		right: 15px;
	}
	#mall-goods-seach .rmd-history .search-rmd{
		padding: 10px;
		background: #fff;
		overflow: hidden;
	}
	#mall-goods-seach .rmd-history .search-rmd div{
		display: inline-block;
		float: left;
		max-width: 187px;
		min-width: 58px;
		height: 31px;
		line-height: 31px;
		margin: 5px;
		padding: 0 16px;
		-webkit-box-sizing: border-box;
		-mox-box-sizing: border-box;
		box-sizing: border-box;
		-webkit-border-radius: 2px;
		-moz-border-radius: 2px;
		border-radius: 2px;
		border: 1px solid #ccc;
		color: #2f2f2f;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		text-align: center;
		font-size: 14px;
	}
	#mall-goods-seach .mall-goods-seach-warp{
		background-color: #fff;
	}
	#mall-goods-seach .mall-goods-seach-warp .goods-list .goods-item{
		position: relative;
		padding: 10px 15px;
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}
	#mall-goods-seach .goods-item .goods-thumb{
		width: 56px;
		height: 56px;
		font-size: 0px;
		border-radius: 3px;
		overflow: hidden;
	}
	#mall-goods-seach .goods-item .goods-info{
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: space-between;
		position: relative;
		width: 80%;
	}
	#mall-goods-seach .goods-item .goods-operation{
		position: absolute;
		bottom: 0;
		right: 0;
	}
	#mall-goods-seach .goods-item .goods-operation .foodop{
		right: 0;
		bottom: 0;
	}
	#mall-goods-seach .goods-item .discount{
		position:relative;
		background: linear-gradient(to right, rgba(253,111,99,1) , rgba(253,111,99,0.5));
		color:#fff!important;
		padding-left:10px;
		font-size: 12px;
		height: 12px;
		width: 100px;
		line-height: 12px;
	}

	#mall-goods-seach .goods-item .goods-info .svip-price-tips{
		display: flex;
		align-items: center;
		line-height: 1;
	}
	#mall-goods-seach .goods-item .goods-info .svip-price-tips .svip-price{
		display: flex;
		align-items: center;
		background-color: #302d22;
		padding: 1px 5px;
		border-top-left-radius: 2px;
		border-bottom-left-radius: 2px;
		color: #fff2bd;
		font-size: 11px;
		border: 1px solid #302d22;
		border-right: none;
	}
	#mall-goods-seach .goods-item .goods-info .svip-price-tips .svip-price span{
		padding-left: 3px;
	}
	#mall-goods-seach .goods-item .goods-info .svip-price-tips .svip-price .icon-crownfill{
		font-size: 11px;
	}
	#mall-goods-seach .goods-item .goods-info .svip-price-tips .svip-activity{
		background-color: #fff2bd;
		color: #1c1a13;
		border: 1px solid #aca275;
		border-left: none;
		border-top-right-radius: 2px;
		border-bottom-right-radius: 2px;
		font-size: 11px;
		padding: 1px 0;
	}
	/*门店会员价格*/
	#mall-goods-seach .kabao-price-wrap{
		display: flex;
		align-items: center;
		height: 18px;
		font-size: 14px;
		line-height: 1;
	}
	#mall-goods-seach .kabao-price-wrap .kabao-price{
		font-weight: bold;
		font-size: 16px;
		color: #705A41;
	}
	#mall-goods-seach .kabao-price-wrap .kabao-label{
		position: relative;
		display: flex;
		align-items: center;
		padding: 2px 5px;
		margin-left: 5px;
		border-radius: 0 3px 3px 3px;
		font-size: 12px;
		background-color: #705A41;
		color: #D6B9A3;
	}
	#mall-goods-seach .kabao-price-wrap .kabao-label:before{
		content: '';
		position: absolute;
		top: 0px;
		left: -4px;
		width: 0;
		height: 0;
		border-width: 5px;
		border-style: solid;
		border-color: #705A41 transparent transparent transparent;
	}
	#mall-goods-seach .cartop{
		position: fixed;
		width: 44px;
		height: 44px;
		border: 1px solid #D5D5D5;
		bottom: 120px;
		left: 15px;
		border-radius: 100%;
		background: #fff;
		z-index: 1000;
	}
	#mall-goods-seach .cartop>a{
		display: inline-block;
		width: 100%;
		height: 100%;
		position: relative;
		border-radius: 100%;
	}
	#mall-goods-seach .cartop .icon{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		font-size: 23px;
		color: #666666;
	}
	#mall-goods-seach .cartop .num{
		position: absolute;
		min-width: 15px;
		height: 15px;
		line-height: 15px;
		font-size: 12px;
		text-align: center;
		top: -1px;
		right: -1px;
		color: #fff;
		background: #FE4C44;
		border-radius: 15px;
		padding: 0 3px;
	}
</style>


